/**
 * 
 */
export function generateMercuryAnalysis(astrologyPositions, language = 'zh') {
  const mercury = astrologyPositions?.mercury;
  
  if (!mercury?.sign) {
    return {
      title: getTranslation('astrology.titleTemplates.mercury', language) || '',
      description: getTranslation('astrology.detailed.mercury.default.description', language) || '',
      risks: getTranslation('astrology.detailed.mercury.default.risks', language) || '',
      techniques: getTranslation('astrology.detailed.mercury.default.techniques', language) || ''
    };
  }

  const signName = mercury.sign;
  const normalizedSign = normalizeSignName(signName);
  const baseKey = `astrology.detailed.mercury.${normalizedSign}`;
  
  return {
    title: generateLocalizedTitle('mercury', signName, language),
    thinking: getTranslation(`${baseKey}.thinking`, language) || `${signName}`,
    communication: getTranslation(`${baseKey}.communication`, language) || `${signName}`,
    learning: getTranslation(`${baseKey}.learning`, language) || ``
  };
}

